<template>
  <PageWrapper dense contentFullHeight fixedHeight contentClass="flex"
   style="position: relative">
    <stationTree @select-tree="selectTree" />
    <div class="right-table-layout">
    <BasicTable @register="registerTable" 
    @resize-column="(w, col) => (col.width = w)"
    @row-dbClick="dbClick"
    class="fix-basic-table"
    @selection-change="handleSelectChange"
    >
      <template #toolbar>
        <a-button type="primary" :preIcon="IconEnum.ADD" @click="handleCreate">
          {{ '添加' }}
        </a-button>
        <a-button class="red-btn" :disabled="selectedRowId == ''" :preIcon="IconEnum.DELETE" @click="handleCancel">
          {{ '删除' }}
        </a-button>
        <a-button class="yellow-btn" :preIcon="IconEnum.EXPORT" @click="handleExport">
          {{ '导出' }}
        </a-button>
      </template>
    </BasicTable>
    </div>
  </PageWrapper>
</template>

<script lang="ts" setup>
	import * as CommonUtil from '/@/utils/common';
  import { PageWrapper } from '/@/components/Page';
  import { useI18n } from '@/hooks/web/useI18n';
  import { useMessage,SysMessage } from '@/hooks/web/useMessage';
  import { IconEnum } from '@/enums/appEnum';
  import { BasicTable, useTable,SorterResult } from '@/components/Table';
  import { MsgManager } from '/@/message/MsgManager';
  import { ref, onMounted } from 'vue';
  import stationTree from '@/views/po/stationtree/stationTree.vue';
  import { setStationAndOrgan } from '@/views/po/stationtree/stationTree';
  import { exportExcelFile } from '@/utils/file/download';
  import { deleteProcessInstance } from '@/api/bpm/processInstance';
  import { addTabPage } from '@/utils/route';
  import { columns, searchFormSchema, getSupervisionPage, exportSupervision } from './index';

  const queryParam: any = ref({
    status: '',
    businessKind: '',
    examinationResult: '',
    billCode: '',
    pageNo: 1,
    pageSize: 10,
    sortField: 'fillinDate',
    sortOrder: 'desc'
  });
  let selectedRowId = ref('');

  const { t } = useI18n();
  const { createConfirm, createMessage } = useMessage();

  function selectTree(data) {
    setStationAndOrgan(data, queryParam, null, reload);
  }

  const [registerTable, { getForm, reload, getPaginationRef,getSelectRows }] = useTable({
    title: '试验检测',
    api: async () => {
      const paginationRef = getPaginationRef() as any;
      queryParam.value.pageNo = paginationRef.current;
      queryParam.value.pageSize = paginationRef.pageSize;
      let dataObj = await getSupervisionPage(queryParam.value);
      return dataObj.result;
    },
    columns,
    rowKey: 'id',
    bordered: true,
    formConfig: { 
      labelWidth: 120, 
      schemas: searchFormSchema,
      showAdvancedButton:true,
      autoAdvancedLine:1,  
    },
    useSearchForm: true,
    showTableSetting: true,
    rowSelection: {
      type: 'radio',
      fixed: true,
      onSelect: onSelect
    },
    sortFn: (sortInfo: SorterResult) => {
      if (sortInfo.order) {
        queryParam.value.sortField = sortInfo.field.replace('Text', '');
        queryParam.value.sortOrder = sortInfo.order.replace('end', '');
      } else {
        queryParam.value.sortField = 'fillinDate';
        queryParam.value.sortOrder = 'desc';
      }
    },
    handleSearchInfoFn(info) {
      for (const key in info) {
        queryParam.value[key] = info[key];
      }
    },

  });

  function onSelect(record, selected) {
    if (selected) {
      selectedRowId.value = record.id;
    } else {
      selectedRowId.value = '';
    }
  }
  function handleSelectChange({ keys, rows }) {
    if (rows && rows.length > 0) {
      const record = rows[0];
      selectedRowId.value = record.id;
    } else {
      selectedRowId.value = '';
    }
  }
  function handleCreate() {
    if (!queryParam.value.stationId) {
      SysMessage.getInstance().info('请选中电站添加');
      return;
    }
    // router.push({
    //   path: `/po/supervision/create?id=`,
    //   query: {
    //     stationId: queryParam.value.stationId,
    //     stationName: queryParam.value.stationName,
    //     stationOrganId: queryParam.value.stationOrganId,
    //     stationOrganName: queryParam.value.stationOrganName,
    //   },
    // });
    addTabPage(`/po/supervision/create?id=`, '添加试验检测', {
      stationId: queryParam.value.stationId,
      stationName: queryParam.value.stationName,
      stationOrganId: queryParam.value.stationOrganId,
      stationOrganName: queryParam.value.stationOrganName,
    });
  }
  const dbClick=(record, index, event)=>{
    
    handleEdit(record);
  }
  function handleEdit(record) {
    // let str='';
    // if(record.status==0){
    //   str='修改'
    // } else if (record.status==1){
    //   str='审批'
    // } else {
    //   str='查看'
    // }
    // addTabPage(
    //   `/po/supervision/create?id=${record.id}&processInstanceId=${record.processInstanceId}`,
    //   str+'试验检测'
    // );
    CommonUtil.toFlowPage(record.id);
  }
  const handleCancel = async () => {
    let rows = getSelectRows();
    if(rows.length != 1){
      SysMessage.getInstance().success('请选择一行数据');
      return;
    }
    let record = rows[0];

    createConfirm({
      title: '提示',
      iconType: 'warning',
      content: '确定要删除选中的记录？',
      async onOk() {
        if(record.status !=0){
          SysMessage.getInstance().success('非草稿状态不能删除');
          return;
        }
        await deleteProcessInstance(record.processInstanceId);
        SysMessage.getInstance().success('删除成功');
        reload();
      },
    });
   
  };

  async function handleExport() {
    createConfirm({
      title: t('common.exportTitle'),
      iconType: 'warning',
      content: t('common.exportMessage'),
      async onOk() {
        let exportParams = getForm().getFieldsValue();
        Object.assign(exportParams,queryParam.value);
        await exportSupervision(exportParams).then((res) => {
          exportExcelFile(res?.data, '试验检测.xls');
        });
        createMessage.success(t('common.exportSuccessText'));
      },
    });
  }
  onMounted(() => {
    MsgManager.getInstance().listen('super-vision',  ()=>{
      reload()
    });
  });
</script>

<style lang="less" scoped>
  .right-table-layout {
    flex: 1;
    width: calc(100% - 308px);
  }

  .fix-basic-table {
    :deep(&.vben-basic-table .ant-table-wrapper) {
      padding: 10px 16px 12px;
    }

    :deep(.table-settings) {
      margin-right: 0;
    }

    :deep(.table-settings .anticon-setting) {
      margin-right: 0;
    }
  }

</style>