import { BasicColumn, FormSchema, useRender } from '@/components/Table';
import { defHttp } from '@/utils/http/axios';
import { useUserStore } from '/@/store/modules/user';

enum Api {
  getSupervisionPage = '/po/technical-supervision/page',
  // deleteSupervisionData = '/po/technical-supervision/delete',
  exportSupervisionData = '/po/technical-supervision/export-excel',
  getSupervisionData = '/po/technical-supervision/get',
}

// 查询安全检查模版列表
export function getSupervisionPage(params) {
  return defHttp.get({ url: Api.getSupervisionPage, params });
}
export function getSupervisionData(params) {
  return defHttp.get({ url: Api.getSupervisionData, params });
}
// 删除安全检查模版
// export function deleteSupervision(id: string) {
//   return defHttp.delete({ url: Api.deleteSupervisionData + '?id=' + id });
// }

// 导出安全检查模版 Excel
export function exportSupervision(params) {
  return defHttp.download(
    { url: Api.exportSupervisionData, params },
    { isReturnNativeResponse: true },
  );
}
const statusColor = {
  0: '#dc0000', // 草稿
  1: '#338ed1', // 处理中
  2: '#30ca5a', // 通过
  3: '#929292', // 不通过
  4: '#929292', // 已取消
  5: '#929292', // 已终止
  6: '#fda941', // 退回/驳回
};
export const columns: BasicColumn[] = [
  { title: '单据编号', dataIndex: 'billCode', width: 180,sorter: true,resizable: true },
  { title: '所属公司', dataIndex: 'stationOrganName',align: 'left',headAlign:'center',
   width: 160,sorter: true,resizable: true,ifShow:useUserStore().isMultiOrganization },
  { title: '电站名称', dataIndex: 'stationName',align: 'left',headAlign:'center', width: 160,sorter: true,resizable: true },
  {
    title: '期数',
    dataIndex: 'periodId',
    width: 160,sorter: true,resizable: true,
    align: 'left',headAlign:'center',
    customRender: ({ record }) => {
      return record['periodName'].join(',');
    },
  },
  { title: '业务类型', dataIndex: 'businessKindText', width: 100,sorter: true,resizable: true },
  { title: '预试单位', dataIndex: 'preExaminationCompany',align: 'left',headAlign:'center', width: 160,sorter: true,resizable: true },
  { title: '本次试验时间', dataIndex: 'startDate', width: 160,sorter: true,resizable: true },
  { title: '本次完成时间', dataIndex: 'endDate', width: 160,sorter: true,resizable: true },
  { title: '试验结果', dataIndex: 'examinationResultText', width: 100,sorter: true,resizable: true },
  { title: '转缺陷数', dataIndex: 'faultNum',align: 'right',headAlign:'center', width: 100,sorter: true,resizable: true },
  {
    title: '审批状态',
    dataIndex: 'statusText',
    width: 100,sorter: true,resizable: true,
    customRender: ({ text, record }) => {
      return useRender.renderTag(text, statusColor[record.status]);
    },
  },
  { title: '创建人', dataIndex: 'personMemberName', width: 100, sorter: true, resizable: true },
  { title: '创建时间', dataIndex: 'fillinDate', sorter: true, resizable: true },
];

